<template>
    <div id="Left_three" style="width:100%;height: 100%;"></div>
</template>

<script>
import * as echarts from 'echarts'
export default {
    mounted:()=>{
        var Left_threeChart = echarts.init(document.getElementById("Left_three"));
        var option = {
            title:{
                text:"读者性别及年龄分布情况",
                textStyle:{
                    color:"white",
                    fontSize:20,
                },
                left:"center",
                top:10
            },
            legend: {
                top:40,
                textStyle:{
                    color:"white"
                },
                itemGap: 80
            },
            grid: {
                top: "30%"
            },
            tooltip: {},
            dataset: {
                source: [
                    ['性别', '男', '女',],
                    ['1-10', 43, 85],
                    ['11-20', 43, 85],
                    ['21-30', 83, 73],
                    ['31-40', 86, 65],
                    ['41-50', 72, 53],
                    ['51-60', 86, 65],
                    ['61-70', 72, 53]
                ]
            },
            xAxis: { 
                type: 'category',
                axisLabel:{
                    color:"white",
                    fontSize:14
                },
                axisLine:{
                    show:false
                },
                axisTick:{
                    show:false
                }
            },
            yAxis: {
                axisLabel:{
                    color:"white"
                },
                name:"单位/个",
                nameTextStyle:{
                    color:"white"
                },
                splitLine: {
                    show: false
                }
            },
            // Declare several bar series, each will be mapped
            // to a column of dataset.source by default.
            series: [
                { 
                    type: 'bar',
                    barWidth: "20%",
                    itemStyle: {
                        borderRadius: [10, 10, 10, 10],
                        color:"#6cfef3"
                    }
                },
                { 
                    type: 'bar',
                    barWidth: "20%",
                    itemStyle: {
                        borderRadius: [10, 10, 10, 10],
                        color:"#74cbff"
                    }
                }
            ]
        };
        option && Left_threeChart.setOption(option)
    }
}
</script>