<template>
  <div id="readers" style="width: 100%;height: 100%;margin: 0 auto;"></div>
</template>

<script>
import * as echarts from 'echarts';
export default {
  mounted:()=>{
            var app = {};

        var chartDom = document.getElementById('readers');
        var myChart = echarts.init(chartDom);
        var option;

        const categories = (function () {
          let now = new Date();
          let res = [];
          let len = 10;
          while (len--) {
            res.unshift(now.toLocaleTimeString().replace(/^\D*/, ''));
            now = new Date(+now - 2000);
          }
          return res;
        })();
        const categories2 = (function () {
          let res = [];
          let len = 10;
          while (len--) {
            res.push(10 - len - 1);
          }
          return res;
        })();
        const data = (function () {
          let res = [];
          let len = 10;
          while (len--) {
            res.push(Math.round(Math.random() * 4));
          }
          return res;
        })();
        const data2 = (function () {
          let res = [];
          let len = 0;
          while (len < 10) {
            res.push(+Math.round(Math.random() * 4));
            len++;
          }
          return res;
        })();
        option = {
          title: {
            text: '实时出入馆人数统计',
              textStyle:{
                color:'white'
              },
              top:"10%",
              left:20,
          },
          grid: {
            top: "30%"
          },
          tooltip: {
            trigger: 'axis',
          },
          legend: {
            textStyle:{
              color:"white"
            },
            top:20
          },
          dataZoom: {
            show: false,
            start: 0,
            end: 100
          },
          xAxis: [
            {
              type: 'category',
              boundaryGap: true,
              data: categories,
              axisLabel:{
                color:'white'
              }
            },
            {
              type: 'category',
              boundaryGap: true,
              data: categories2,
              show:false
            }
          ],
          yAxis: [
            {
              type: 'value',
              scale: true,
              name: '入馆',
              max: 10,
              min: 0,
              boundaryGap: [0.2, 0.2],
              axisLabel:{
                color:'white'
              },
              nameTextStyle:{
                color:'white'
              }
            },
            {
              type: 'value',
              scale: true,
              name: '出馆',
              max: 10,
              min: 0,
              boundaryGap: [0.2, 0.2],
              axisLabel:{
                color:'white'
              },
              nameTextStyle:{
                color:'white'
              }
            }
          ],
          series: [
            {
              name: '入馆人数',
              type: 'bar',
              xAxisIndex: 1,
              yAxisIndex: 1,
              data: data,
              color:"#1089e7"
            },
            {
              name: '出馆人数',
              type: 'line',
              data: data2,
              color:"#f57474"
            }
          ]
        };
        app.count = 11;
        setInterval(function () {
          let axisData = new Date().toLocaleTimeString().replace(/^\D*/, '');
          data.shift();
          data.push(Math.round(Math.random() * 4));
          data2.shift();
          data2.push(+Math.round(Math.random() * 4));
          categories.shift();
          categories.push(axisData);
          categories2.shift();
          categories2.push(app.count++);
          myChart.setOption({
            xAxis: [
              {
                data: categories
              },
              {
                data: categories2
              }
            ],
            series: [
              {
                data: data
              },
              {
                data: data2
              }
            ]
          });
        }, 2100);

        option && myChart.setOption(option);
  }
}
</script>