import { createRouter, createWebHashHistory} from 'vue-router'
import Home_view from './components/Home_view.vue';
import Reader_insight from './components/Reader_insight.vue'
import Recommended_books from './components/Recommended_books.vue';
//创建一个路由
const router = createRouter({
    //路由的模式
    history:createWebHashHistory(),
    routes:[
        {
            //跳转首页
            path:"/",
            component:Home_view
        },
        {
            //跳转读者洞察页面
            path:"/Reader_insight",
            component:Reader_insight
        },
        {
            //跳转书海探珍页面
            path:"/Recommended_books",
            component:Recommended_books
        }
    ]
});
//暴露路由
export default router;