<template>
    <div id="Center_chart" style="width: 95%;height: 95%;"></div>
</template>

<script>
import * as echarts from 'echarts';
export default {
    mounted:()=>{
        var Center_chart = document.getElementById('Center_chart');
        var myChart = echarts.init(Center_chart);
        var option = {
            // color:['#e57373','#f06292','ba68c8','#9575cd','#9fa8da','#90caf9','#81d4fa','#80deea','#80cbc4','#a5d6a7','#c5e1a5','#e6ee9c','#fff59d','#ffe082','#ffcc80','#ffab91'],
            tooltip: {
                trigger: 'item',
                formatter: '{a} <br/>{b}: {c} ({d}%)'
            },
            legend: {
                data: [
                '天才在左 疯子在右',
                '平凡的世界',
                '时间简史',
                '活着',
                '围城',
                'iOS编程',
                'PHP实战',
                '汽车文化',
                '汽车拆装实训'
                ],
                formatter: [
                    '{a|{name}}'
                ].join('\n'),
                textStyle:{
                    color:'white',
                    rich: {
                        a: {
                            width: 120,
                            lineHeight: 12
                        }
                    }
                }
            },
            series: [
                {
                name: '推荐人数',
                type: 'pie',
                selectedMode: 'single',
                radius: [0, '30%'],
                label: {
                    position: 'inner',
                    fontSize: 14,
                    textStyle:{
                        color:"white"
                    }
                },
                labelLine: {
                    show: false
                },
                data: [
                    { value: 2256, name: '工业技术' },
                    { value: 1601, name: '文学' },
                    { value: 1091, name: '交通运输', selected: true }
                ],
                },
                {
                name: '推荐人数',
                type: 'pie',
                radius: ['45%', '60%'],
                labelLine: {
                    length: 40
                },
                label: {
                    formatter: '{a|{a}}{abg|}\n{hr|}\n  {b|{b}：}{c}  {per|{d}%}  ',
                    backgroundColor: '#F6F8FC',
                    borderColor: '#8C8D8E',
                    borderWidth: 1,
                    borderRadius: 4,
                    rich: {
                    a: {
                        color: '#6E7079',
                        lineHeight: 22,
                        align: 'center'
                    },
                    hr: {
                        borderColor: '#8C8D8E',
                        width: '100%',
                        borderWidth: 1,
                        height: 0
                    },
                    b: {
                        color: '#4C5058',
                        fontSize: 14,
                        fontWeight: 'bold',
                        lineHeight: 33
                    },
                    per: {
                        color: '#fff',
                        backgroundColor: '#4C5058',
                        padding: [3, 4],
                        borderRadius: 4
                    }
                    }
                },
                data: [
                    { value: 1283, name: 'PHP实战' },
                    { value: 973, name: 'iOS编程' },
                    { value: 513, name: '天才在左 疯子在右' },
                    { value: 223, name: '平凡的世界' },
                    { value: 233, name: '时间简史' },
                    { value: 412, name: '活着' },
                    { value: 220, name: '围城' },
                    { value: 635, name: '汽车文化' },
                    { value: 456, name: '汽车拆装实训' }
                ]
                }
            ]
            };
        option && myChart.setOption(option);
    }
}
</script>