<template>
    <div id="Right_one" style="width:100%;height: 100%;"></div>
</template>

<script>
import * as echarts from 'echarts'
export default {
    mounted:()=>{
        var Right_oneChart = echarts.init(document.getElementById("Right_one"));
        var option = {
            title: {
                text: '读者活跃情况对比',
                textStyle:{
                    color:"white"
                },
                left:"center",
                top:20
            },
            tooltip: {
                trigger: 'axis',
            },
            legend: {
                data: ['工作日', '周末'],
                top:"20%",
                textStyle:{
                    color:"white"
                },
                itemGap: 80
            },
            toolbox: {
                feature: {
                saveAsImage: {}
                }
            },
            grid: {
                top:"40%",
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis: [
                {
                type: 'category',
                boundaryGap: false,
                data: ['8-10点', '10-12点', '12-14点', '14-16点', '16-18点', '18-20点', '20-22点'],
                axisLabel:{
                    color:"white"
                },
                axisLine:{
                    show:false
                },
                axisTick:{
                    show:false
                }
                }
            ],
            yAxis: [
                {
                type: 'value',
                axisLabel:{
                    color:"white"
                },
                splitLine: {
                    show: false
                },
                name:"单位/个",
                nameTextStyle:{
                    color:"white"
                }
                }
            ],
            series: [
                {
                name: '工作日',
                type: 'line',
                stack: 'Total',
                areaStyle: {},
                emphasis: {
                    focus: 'series'
                },
                data: [1200, 1320, 1010, 1340, 900, 2300, 2100],
                color:"#ee6666",
                },
                {
                name: '周末',
                type: 'line',
                stack: 'Total',
                areaStyle: {},
                emphasis: {
                    focus: 'series'
                },
                color:"#74cbff",
                data: [2200, 1820, 1910, 2340, 2900, 3300, 3100]
                }
            ]
        };
        option && Right_oneChart.setOption(option)
    }
}
</script>