<template>
    <div id="Right_two" style="width: 100%;height: 100%;"></div>
</template>
<script>
import * as echarts from 'echarts';
export default {
    mounted:()=>{
        var Right_two = echarts.init(document.getElementById("Right_two"));
        var option = {
            tooltip:{},
            title:{
                text:"搜索热词排行",
                top:20,
                left:'center',
                textStyle:{
                    color:"white"
                }
            },
            dataset: [
                {
                dimensions: ['关键词', '数量', '分类', '最近一次查询'],
                source: [
                    ['人工智能', 410, 'Engineer', '2011-02-12'],
                    ['医药', 200, 'Teacher', '2011-03-01'],
                    ['巴金', 520, 'Musician', '2011-02-14'],
                    ['鲁迅', 370, 'Teacher', '2011-02-18'],
                    ['古籍', 250, 'Engineer', '2011-04-02'],
                    ['小说', 250, 'Engineer', '2011-04-02'],
                    ['古诗词', 250, 'Engineer', '2011-04-02'],
                ]
                },
                {
                transform: {
                    type: 'sort',
                    config: { dimension: '数量', order: 'desc' }
                }
                }
            ],
            xAxis: {
                type: 'category',
                axisLabel: { interval: 0, rotate: 30 ,color:"white"}
            },
            yAxis: {
                axisLabel:{
                    color:"white"
                }
            },
            series: {
                type: 'bar',
                encode: { x: '关键词', y: '数量' },
                datasetIndex: 1,
                itemStyle:{
                    color:function(parmas){
                        var colorList = ['#ee6666','#73c0de','#3ba272','#fc8452','#9a60b4','#ea7ccc','#fac858'];
                        return colorList[parmas.dataIndex]
                    }
                }
            }
        };
        option && Right_two.setOption(option)
    }
}
</script>