<template>
    <div id="Left_two" style="width:100%;height: 100%;"></div>
</template>

<script>
import * as echarts from 'echarts'
export default {
    mounted:()=>{
        var Left_twoChart = echarts.init(document.getElementById("Left_two"));
        var option = {
            title: {
                text: '读者身份',
                subtext: '各占比例',
                left: 'center',
                textStyle:{
                    color:"white"
                },
                subtextStyle:{
                    color:"white"
                },
                top:20
            },
            tooltip: {
                trigger: 'item',
                formatter: "{b} <br/> {d}%"
            },
            legend: {
                orient: 'vertical',
                right:5,
                top:"center",
                textStyle:{
                    color:"white"
                },
            },
            label:{color:"white"},
            series: [
                {
                name: '身份',
                type: 'pie',
                radius: [0, 80],
                top:40,
                data: [
                    { value: 1048, name: '学生' },
                    { value: 735, name: '教师' },
                    { value: 580, name: '科研人员' },
                    { value: 484, name: '作家' },
                    { value: 300, name: '其他' }
                ],
                emphasis: {
                    itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
                }
            ]
        };
        option && Left_twoChart.setOption(option)
    }
}
</script>