<template>
    <div id="Center_one" style="width: 100%;height: 100%;"></div>
</template>

<script>
import * as echarts from 'echarts';
export default {
    mounted:()=>{
        var Center_oneChart = echarts.init(document.getElementById("Center_one"));
        var option = {
            color:['#2f54eb','#1890ff','#13c2c2'],
            title: {
                text: '馆舍占地面积',
                top:-5,
                left: 'center',
                textStyle:{
                    color:"white"
                }
            },
            tooltip: {
                trigger: 'item'
            },
            legend: {
                orient: 'horizontal',
                x:'center',
                y:'bottom',
                textStyle:{
                    color:"white"
                }
            },
            series: [
                {
                name: '占地面积/平方米',
                type: 'pie',
                radius: '50%',
                label:{
                    textBorderColor:"#fff",
                    color:'white'
                },
                data: [
                    { value: 3850, name: '南馆' },
                    { value: 2000, name: '西馆' },
                    { value: 22000, name: '北馆' }
                ],
                emphasis: {
                    itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
                }
            ]
        };
        Center_oneChart.setOption(option);
    }
}
</script>