<template>
    <div id="bottom_center_two" style="width: 100%;height: 100%; margin: 0px auto; margin-top: 20px;"></div>
</template>
<script>
import * as echarts from 'echarts';
export default {
    mounted:()=>{
        var bottom_center_twoChart = echarts.init(document.getElementById("bottom_center_two"));
        var option = {
            title:{
                text:'人数楼层分布',
                textStyle:{
                    color:'white'
                },
                top:"0",
                left:"center",
                },
            tooltip: {},
            xAxis: {
                type: 'category',
                data: ['一层', '二层', '三层', '四层', '五层', '六层', '七层'],
                axisLabel:{
                    color:'white'
                }
            },
            yAxis: {
                type: 'value',
                nameLocation: "end",
                name: "人数",
                nameTextStyle:{
                    color:"white"
                },
                axisLabel:{
                    color:'white'
                }
            },
            series: [
                {
                data: [120, 200, 150, 80, 70, 110, 130],
                type: 'bar',
                color:"#74cbff"
                }
            ]
        }
        option && bottom_center_twoChart.setOption(option);
    }
}
</script>