<template>
    <div id="Right_one" style="width:100%;height: 100%;"></div>
</template>

<script>
import * as echarts from 'echarts'
export default {
    mounted:()=>{
        var datas = [
        ////////////////////////////////////////
        [
            { name: '文学类', value: 5600 },
            { name: '历史类', value: 10000 },
            { name: '医学类', value: 8000 },
            { name: '法律类', value: 5000 },
            { name: '综合类', value: 5000 },
            { name: '艺术类', value: 3800 }
        ]
        ];
        var Right_oneChart = echarts.init(document.getElementById("Right_one"));
        var option = {
            title: {
                text: '借阅时长分布',
                left: 'center',
                textStyle: {
                    color: 'white',
                    fontSize: 18
                },
                top:20
            },
            series: datas.map(function (data, idx) {
                var top = idx * 33.3;
                return {
                type: 'pie',
                radius: [40, 120],
                top: top + '%',
                height: '100%',
                left: 'center',
                width: 400,
                itemStyle: {
                    borderColor: '#fff',
                    borderWidth: 1
                },
                label: {
                    alignTo: 'edge',
                    formatter: '{name|{b}}\n{time|{c} 小时}',
                    minMargin: 5,
                    edgeDistance: 10,
                    lineHeight: 15,
                    rich: {
                    time: {
                        fontSize: 10,
                        color: '#999'
                    }
                    },
                    color:'white'
                },
                labelLine: {
                    length: 15,
                    length2: 0,
                    maxSurfaceAngle: 80
                },
                labelLayout: function (params) {
                    const isLeft = params.labelRect.x < Right_oneChart.getWidth() / 2;
                    const points = params.labelLinePoints;
                    // Update the end point.
                    points[2][0] = isLeft
                    ? params.labelRect.x
                    : params.labelRect.x + params.labelRect.width;
                    return {
                    labelLinePoints: points
                    };
                },
                data: data
                };
            })
        }
        option && Right_oneChart.setOption(option)
    }
}
</script>