<template>
    <div id="home_main">
        <div id="main_top">
            <div class="top_box">
                <dv-border-box-2>
                    <top_left_one/>
                </dv-border-box-2>
            </div>
            <div class="top_box">
                <dv-border-box-1>
                    <div class="box_item">
                    <h2>总借出数量</h2>
                    <p class="item_count_jie">{{zong}}</p>
                    <div class="item_dbox">
                        <div class="item">
                            <h3>今日借出</h3>
                            <p>
                                <span class="item_t">{{b}}</span>
                                <span class="item_t">{{s}}</span>
                                <span class="item_t">{{g}}</span>
                            </p>
                        </div>
                        <div class="item">
                            <h3>本月借出</h3>
                            <p>
                                <span class="item_t">{{q}}</span>
                                <span class="item_t">{{b}}</span>
                                <span class="item_t">{{s}}</span>
                                <span class="item_t">{{g}}</span>
                            </p>
                        </div>
                    </div>
                </div>
                    <div class="box_item">
                        <top_center_one/>
                    </div>
                </dv-border-box-1>
            </div>
            <div class="top_box">
                
                <dv-border-box-1>
                    <div class="box_item">
                    <h2>总还入数量</h2>
                    <p class="item_count_huan ">{{zong}}</p>
                    <div class="item_dbox">
                        <div class="item">
                            <h3>今日还入</h3>
                            <p>
                                <span class="item_t">{{ b }}</span>
                                <span class="item_t">{{ s }}</span>
                                <span class="item_t">{{ g }}</span>
                            </p>
                        </div>
                        <div class="item">
                            <h3>本月还入</h3>
                            <p>
                                <span class="item_t">{{ q }}</span>
                                <span class="item_t">{{ b }}</span>
                                <span class="item_t">{{ s }}</span>
                                <span class="item_t">{{ g }}</span>
                            </p>
                        </div>
                    </div>
                </div>
                <div class="box_item">
                    <top_center_two/>
                </div>
                </dv-border-box-1>
            </div>
            <div class="top_box">
                <dv-border-box-2>
                    <div class="Title">
                        <h3>实时借还动态</h3>
                        <top_right_one/>
                    </div>
                </dv-border-box-2>
            </div>
        </div>

        <div id="main_bottom">
            <div class="btm1">
                <dv-border-box-13>
                    <bottom_left/>
                </dv-border-box-13>
            </div>
            <div class="btm2">
                <dv-border-box-8>
                    <div class="btm2_box">
                        <div class="Title">
                            <h3>馆内负载</h3>
                            <bottom_center_one/>
                        </div>
                        <div>
                            <bottom_center_two/>
                        </div>
                    </div>
                </dv-border-box-8>
            </div>
            <div class="btm3">
                <dv-border-box-13>
                    <div class="Title">
                        <h3>读者借阅Top10</h3>
                        <bottom_right/>
                    </div>
                </dv-border-box-13>
            </div>
        </div>
    </div>
</template>
<script>
import top_center_one from '@/views/Home/top_center_one.vue';
import top_center_two from '@/views/Home/top_center_two.vue';

import top_left_one from '@/views/Home/top_left_one.vue';
import top_right_one from '@/views/Home/top_right_one.vue';

import bottom_left from '@/views/Home/bottom_left.vue';
import bottom_right from '@/views/Home/bottom_right.vue';

import bottom_center_one from "@/views/Home/bottom_center_one.vue"
import bottom_center_two from "@/views/Home/bottom_center_two.vue"
export default{
    components:{
        top_center_one,
        top_center_two,
        top_left_one,
        top_right_one,

        bottom_right,
        bottom_left,
        
        bottom_center_one,
        bottom_center_two,
    },
    data(){
        return{
            zong:123456,
            g:4,
            s:3,
            b:2,
            q:1,
        }
    },
    mounted(){
        setInterval(() => {
            this.zong++;
            this.g++;
            if(this.g % 10 == 0){
                this.s++;
                this.g=0;
            }
            if(this.s % 10 == 0 && this.s != 0){
                this.b++;
                this.s=0;
            }
            if(this.b % 10 == 0 && this.b != 0){
                this.q++;
                this.b=0;
            }
        }, 1000);
    }
}
</script>
<style scoped>
