    display: flex;
    justify-content: space-around;
}
/* 总借出还入 end */

/* 实时借还动态 start */
.Title h3 {
    text-align: center;
    padding: 20px 0px;
}
/* 实时借还动态 end */
/* 底部 */
#main_bottom {
    width: calc(100% - 40px);
    height: 100%;
}
#home_main #main_bottom {
    width: calc(100% - 40px);
    height: 100%;
    display: grid;
    grid-template-columns: 1.5fr 1fr 1.5fr;
    grid-gap: 0px 10px;
}
.btm2 .btm2_box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
}
.btm2 .btm2_box > div {
    width: 100%;
