<template>
    <div id="CenterTwoBox" style="width: 100%;height: 100%;"></div>
</template>

<script>
import * as echarts from 'echarts';
export default {
    mounted:()=>{
        var myColor = ["#1089E7", "#F57474", "#56D0E3", "#F8B448", "#8B78F6"];
        let CenterTwoChart = echarts.init(document.getElementById("CenterTwoBox"));
        var option = {
            title:{
                text:'馆藏',
                left:'center',
                textStyle:{
                    color:"white"
                }
            },
            grid: {
                top: "20%",
                left: "30%",
                right: "22%",
                bottom: "10%"
            },
            xAxis: {
                show: false
            },
            yAxis: [
                {
                    type: "category",
                    inverse: true,
                    data: ["纸质图书", "电子图书", "纸质期刊", "报纸", "博硕学位论文"],
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        color: "#fff"
                    }
                },
                {
                    data: ['99万册', "62万册", "1100种", "100种", "41.67万篇"],
                    inverse: true,
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        color: "#fff"
                    }
                }
            ],
            series: [
                {
                    name: "条",
                    type: "bar",
                    data: [99, 62, 50, 38, 40],
                    yAxisIndex: 0,
                    itemStyle: {
                        barBorderRadius: 20,
                        color: function(params) {
                            return myColor[params.dataIndex];
                        }
                    },
                    barCategoryGap: 50,
                    barWidth: 10,
                    label: {
                        show: true,
                        position: "inside",
                        formatter: "{c}%",
                        textStyle:{
                            color:"#fff"
                        },
                    }
                },
                {
                    name: "框",
                    type: "bar",
                    barCategoryGap: 50,
                    barWidth: 15,
                    yAxisIndex: 1,
                    data: [100, 100, 100, 100, 100],
                    itemStyle: {
                    color: "none",
                    borderColor: "#00c1de",
                    borderWidth: 3,
                    barBorderRadius: 15
                    }
                }
            ]
        };
        CenterTwoChart.setOption(option);
    }
}
</script>