<template>
    <div id="Right_three" style="width:100%;height: 100%;"></div>
</template>

<script>
import * as echarts from 'echarts'
export default {
    mounted:()=>{
        var Right_threeChart = echarts.init(document.getElementById("Right_three"));
        var option = {
            color:['#73c0de'],
            title:{
                text:"馆藏与读者总量",
                textStyle:{
                    color:"white"
                },
                left:"center",
                top:20
            },
            tooltip: {},
            xAxis: {
                type: 'category',
                data: ['馆藏总量', '外借馆藏量', '可续借总量', '读者总量'],
                axisTick:{
                    show:false
                },axisLabel:{
                    color:"white"
                }
            },
            yAxis: {
                type: 'value',
                axisLabel:{
                    color:"white"
                },
                splitLine: {
                    show: false
                },
                axisLine:{
                    show:true
                }
            },
            grid:{
                left:"15%",
                top:"15%",
                bottom:"15%"
            },
            series: [
                {
                data: [90000, 45000, 38000, 60000],
                barWidth: "35%",
                type: 'bar',
                itemStyle: {
                    borderRadius: [25, 25, 25, 25],
                    color:'#73c0de',
                }
                }
            ]
        };
        option && Right_threeChart.setOption(option)
    }
}
</script>