<template>
    <div id="Left_one" style="width:100%;height: 100%;"></div>
</template>

<script>
import * as echarts from 'echarts'
export default {
    mounted:()=>{
        var datas = [
        ////////////////////////////////////////
        [
            { name: '博士生', value: 6 },
            { name: '硕士生', value: 12 },
            { name: '本科生', value: 18 },
            { name: '专业生', value: 23 },
            { name: '中小学或以下', value: 33 },
            { name: '其他', value: 8 }
        ]
        ];
        var Left_oneChart = echarts.init(document.getElementById("Left_one"));
        var option = {
            tooltip:{
                formatter: "{b} <br/> {d}%"
            },
            title: {
                text: '成年读者学历分析',
                left: 'center',
                textStyle: {
                    color: 'white',
                    fontSize: 20
                },
                top:30
            },
            series: datas.map(function (data) {
                var top = 10;
                return {
                type: 'pie',
                radius: [0, 80],
                top: top + '%',
                height: '100%',
                left: 'center',
                width: 330,
                itemStyle: {
                    borderColor: '#fff',
                    borderWidth: 1
                },
                label: {
                    alignTo: 'edge',
                    formatter: '{b}\n{c}%',
                    minMargin: 5,
                    edgeDistance: 10,
                    lineHeight: 15,
                    rich: {
                        time: {
                            fontSize: 10,
                            color: '#999'
                        }
                    },
                    color:"white"
                },
                labelLine: {
                    length: 15,
                    length2: 0,
                    maxSurfaceAngle: 80
                },
                labelLayout: function (params) {
                    const isLeft = params.labelRect.x < Left_oneChart.getWidth() / 2;
                    const points = params.labelLinePoints;
                    // Update the end point.
                    points[2][0] = isLeft
                    ? params.labelRect.x
                    : params.labelRect.x + params.labelRect.width;
                    return {
                    labelLinePoints: points
                    };
                },
                data: data
                };
            })
        }
        option && Left_oneChart.setOption(option)
    }
}
</script>