<template>
    <div id="Right_two" style="width:100%;height: 100%;"></div>
</template>

<script>
import * as echarts from 'echarts'
export default {
    mounted:()=>{
        var Right_twoChart = echarts.init(document.getElementById("Right_two"));
        var option = {
            title:{
                text:"分类借阅比例",
                left: 'center',
                top:20,
                textStyle:{
                    color:"white"
                },
            },
            tooltip: {
                trigger: 'item',
                formatter: "{b} <br/> {d}%"
            },
            legend: {
                orient: 'vertical',
                left: 30,
                top:"center",
                textStyle:{
                    color:"white"
                },
            },
            series: [
                {
                name: '分类借阅比例',
                type: 'pie',
                radius: ['40%', '70%'],
                top:20,
                avoidLabelOverlap: false,
                itemStyle: {
                    borderRadius: 10,
                    borderColor: '#fff',
                    borderWidth: 2
                },
                label: {
                    show: false,
                    position: 'center',
                },
                emphasis: {
                    label: {
                    show: true,
                    fontSize: 40,
                    fontWeight: 'bold',
                    textStyle:{
                        color:"white"
                    }
                    }
                },
                labelLine: {
                    show: false
                },
                data: [
                    { value: 30, name: '文学' },
                    { value: 25, name: '历史' },
                    { value: 15, name: '哲学' },
                    { value: 25, name: '小说' },
                    { value: 20, name: '军事' }
                ]
                }
            ]
        };
        option && Right_twoChart.setOption(option)
    }
}
</script>